* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, p {
  margin: 0; }

.container {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  margin-top: 4rem; }

.header {
  background-color: #ed1e24;
  width: 100vw;
  position: fixed;
  top: 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
  padding: 0 1.5rem;
  & a:first-of-type {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
    padding: .5rem; }
  & input {
    margin: .75rem;
    padding: .25rem; } }

.pokemonCard {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  border-radius: .3rem;
  width: 25rem;
  max-width: 100%;
  margin-top: 1rem;
  box-shadow: 0px 2px 5px 1px #0000002e;
  .pokemonCardTop {
    padding: .25rem .5em;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    width: 100%;
    border-radius: .3rem;
    box-shadow: 0px 2px 5px 1px #eff4f5;
    background-color: #ddd;
    .pokemonIndex {}

    .pokemonName {} }

  .pokemonCardBot {
    .pokemonSprite {
      & img {
        max-width: 100%; } } }

  & h2::first-letter {
    text-transform: capitalize; } }
